@charset "UTF-8";
:root {
  --c-primary: var(--e-global-color-primary);
  --c-secondary: var(--e-global-color-secondary);
  --c-grey: var(--e-global-color-text);
  --c-accent: var(--e-global-color-accent);
  --c-orange: var(--e-global-color-31c33f9);
  --c-dark: var(--e-global-color-c75e4e6);
  --c-light: var(--e-global-color-d5daea7);
  --c-light-gray: var(--e-global-color-2817fd4);
  --c-blue-sky: var(--e-global-color-11d843b);
  --font-size: 16px;
  --font-family: "Open Sans", sans-serif;
}

html {
  overflow-x: hidden;
  font-size: var(--font-size);
  margin-top: 0 !important;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  position: relative;
  scroll-behavior: smooth;
  line-height: 1.625rem;
  font-family: var(--font-family);
}

* {
  margin: 0;
  padding: 0;
  outline: none !important;
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  margin-top: 0;
}
h1:not(:last-child),
h2:not(:last-child),
h3:not(:last-child),
h4:not(:last-child),
h5:not(:last-child),
h6:not(:last-child) {
  margin-bottom: 15px;
}

h1 {
  font-size: 38px;
  line-height: 50px;
}
@media screen and (min-width: 768px) {
  h1 {
    font-size: 32px;
    line-height: 40px;
  }
}

h2 {
  font-size: 32px;
  line-height: 42px;
}
@media screen and (min-width: 768px) {
  h2 {
    font-size: 28px;
    line-height: 36px;
  }
}

h3 {
  font-size: 24px;
  line-height: 34px;
}
@media screen and (min-width: 768px) {
  h3 {
    font-size: 20px;
    line-height: 28px;
  }
}

h4 {
  font-size: 20px;
  line-height: 30px;
}
@media screen and (min-width: 768px) {
  h4 {
    font-size: 18px;
    line-height: 26px;
  }
}

h5 {
  font-size: 18px;
  line-height: 26px;
}
@media screen and (min-width: 768px) {
  h5 {
    font-size: 16px;
    line-height: 24px;
  }
}

h6 {
  font-size: 12px;
  line-height: 16px;
}
@media screen and (min-width: 768px) {
  h6 {
    font-size: 12px;
    line-height: 16px;
  }
}

.container {
  max-width: 1140px;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.no-scroll {
  overflow: hidden;
  height: 100vh;
}

::-moz-selection {
  background: #000;
  color: #fff;
}

::selection {
  background: #000;
  color: #fff;
}

::-moz-selection {
  background: #000;
  color: #fff;
}

::-webkit-input-placeholder {
  color: var(--c-grey);
}

::-moz-placeholder {
  color: var(--c-grey);
}

:-ms-input-placeholder {
  color: var(--c-grey);
}

:-moz-placeholder {
  color: var(--c-grey);
}

:focus {
  outline: 0;
}

input {
  width: 100%;
  border: 1px solid rgba(81, 81, 81, 0.4);
  border-radius: 0;
  position: relative;
  height: 40px;
  padding: 0 10px;
  color: var(--e-global-color-c75e4e6);
}
input:focus {
  outline: none;
  border-color: rgba(24, 69, 91, 0.7);
  box-shadow: 0 0 2px rgba(24, 69, 91, 0.3);
}
input::placeholder {
  color: var(--e-global-color-text);
}

button {
  transition: "all .3s ease";
}

a {
  color: var(--c-dark);
  outline: 0;
  transition: "all .3s ease";
  text-decoration: none;
  line-height: 1.25rem;
}
a:hover, a:focus {
  color: var(--c-active);
  text-decoration: none;
}

p {
  line-height: 1.625rem;
}

strong,
b {
  font-weight: 700;
  color: var(--c-dark);
}

ul,
ol {
  list-style-position: inside;
}

ol ol,
ol ul,
ul ol,
ul ul {
  margin-top: 13px;
  margin-left: 25px;
}

/* typography
---------------------------*/
img {
  max-width: 100%;
  height: auto;
}

.overflow-full {
  overflow: hidden;
}

.monospace {
  font-family: "Source Code Pro", sans-serif;
  color: #494949;
}

audio,
video,
iframe {
  max-width: 100%;
}
@media screen and (min-width: 576px) {
  audio,
  video,
  iframe {
    min-width: 200px !important;
  }
}

pre {
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  background: var(--c-light-grey);
  color: var(--c-dark);
  font-size: 14px;
  line-height: 1.6;
  margin-bottom: 15px;
  max-width: 100%;
  overflow: auto;
  padding: 25px;
}
pre * {
  margin: 0;
}
pre code {
  background: transparent;
}

table {
  border-collapse: separate;
  border-spacing: 0;
  border-width: 1px 0 0 1px;
  margin: 0 0 30px;
  table-layout: fixed;
  width: 100%;
  word-break: break-word;
}

th,
td {
  padding: 7px 10px 5px;
}

table,
th,
td {
  border: 1px solid rgba(var(--ca-dark), 0.15);
  border-collapse: collapse;
}

th {
  font-weight: 600;
  color: var(--c-dark);
}

dt {
  font-weight: 600;
  color: var(--c-dark);
}

dd {
  margin: 0 0 15px;
}

address {
  font-style: italic;
  margin-bottom: 15px;
}

abbr,
acronym {
  border-bottom: 1px dotted var(--c-light-grey);
  cursor: help;
}

code {
  background-color: var(--c-light-grey);
  color: var(--c-dark);
  font-size: 14px;
  padding: 3px 4px 4px;
  border-radius: 5px;
}

mark,
ins {
  background: var(--c-active);
  color: var(--c-light);
  padding: 0 4px;
  text-decoration: none;
}

kbd {
  font-weight: 100;
  -webkit-box-shadow: none;
  box-shadow: none;
  font-size: 14px;
  padding: 3px 4px 4px;
  border-radius: 5px;
  background-color: var(--c-light-grey);
  color: var(--c-dark);
}

tt {
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 14px;
  padding: 3px 4px 4px;
  border-radius: 5px;
  background-color: var(--c-light-grey);
  color: var(--c-dark);
}

select {
  vertical-align: middle;
  min-width: 100%;
  background: var(--c-light);
  color: var(--c-grey);
  border: 1px solid rgba(var(--ca-dark), 0.15);
  outline: none !important;
  max-height: calc(1.2em + 17px);
  height: calc(1.2em + 17px);
  padding: 6px 10px 5px;
  -webkit-appearance: none;
  background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='100' height='100' fill='%23dddddd'><polygon points='0,0 100,0 50,50'/></svg>");
  background-size: 12px;
  background-position: calc(100% - 10px) 16px;
  background-repeat: no-repeat;
  max-width: 100%;
}
select:focus {
  outline-offset: 0 !important;
}

.wp-block-table.is-style-stripes td,
.wp-block-table.is-style-stripes th {
  border-color: rgba(var(--ca-dark), 0.15);
}

.wp-block-latest-comments__comment {
  font-size: inherit;
  line-height: inherit;
}
.wp-block-latest-comments__comment-excerpt p {
  font-size: inherit;
  line-height: inherit;
}

.wp-block-rss {
  list-style: none;
}

.wp-block-button.is-style-squared .wp-block-button__link {
  border-radius: 0;
}

.wp-block-calendar {
  font-family: inherit;
  margin-bottom: 30px;
}
.wp-block-calendar table {
  font-family: inherit;
  margin-bottom: 0;
}
.wp-block-calendar table th {
  text-align: center;
  font-weight: 600;
  border: 1px solid rgba(var(--ca-dark), 0.15);
}
.wp-block-calendar table td {
  border: 1px solid rgba(var(--ca-dark), 0.15);
}
.wp-block-calendar a {
  text-decoration: none;
  font-weight: 600;
}

.fb_iframe_widget > * {
  max-width: 100%;
}

.bypostauthor {
  font-size: inherit;
}

.text-center,
.aligncenter {
  text-align: center;
}

.text-left,
.alignleft {
  text-align: left;
}

.text-right,
.alignright {
  text-align: right;
}

.wp-block-image.is-resized.alignleft,
.alignleft {
  float: left;
  margin: 10px 25px 25px 0;
}

.wp-block-image.is-resized.alignright,
.alignright {
  float: right;
  margin: 10px 0 25px 25px;
}

.alignnone,
div.alignnone {
  margin-top: 10px;
  margin-bottom: 10px;
}

.aligncenter,
div.aligncenter {
  display: block;
  margin: 25px auto 25px auto;
}

.alignwide {
  clear: both;
}

.no-menu > a {
  color: var(--c-active);
}

blockquote {
  border-top: 1px solid rgba(var(--ca-grey), 0.15);
  border-bottom: 1px solid rgba(var(--ca-grey), 0.15);
  margin: 1rem 0 2rem;
  padding: 2rem 0;
}
blockquote.has-text-color * {
  color: inherit;
}
blockquote cite {
  font-size: 1rem;
  font-weight: 400;
  color: var(--c-grey);
  font-style: normal;
  display: block;
  margin-top: 2rem;
}
blockquote cite::before {
  content: "―";
  margin-right: 5px;
}
blockquote cite > * {
  font-size: inherit !important;
  font-style: normal;
}

.wp-block-pullquote blockquote {
  border-color: inherit;
}

h1:first-child,
h2:first-child,
h3:first-child,
h4:first-child,
h5:first-child,
h6:first-child {
  margin-top: 0;
}

.notfound {
  text-decoration: none;
  transition: all 300ms ease;
}
.notfound:hover {
  opacity: 0.7;
}

abbr,
acronym {
  border-bottom: none !important;
}

.overflow {
  overflow: hidden;
}

.video-container iframe {
  border: 0;
}

iframe[name=advpp_manager] {
  position: absolute;
  left: -9999px;
}

.s-back-switch {
  background-size: cover;
  background-position: center;
}

.screen-reader-text {
  display: none;
}

.single-post .gallery {
  margin: 60px -15px 45px;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
  text-align: center;
}
.gallery-item {
  padding: 15px;
  margin: 0;
  width: 100%;
}
.gallery.gallery-columns-2 .gallery-item {
  width: 50%;
}
.gallery.gallery-columns-3 .gallery-item {
  width: -webkit-calc(100% / 3);
  width: 33.3333333333%;
}
@media screen and (min-width: 576px) {
  .gallery.gallery-columns-3 .gallery-item {
    width: 50%;
  }
}
.gallery.gallery-columns-4 .gallery-item {
  width: 25%;
}
@media screen and (min-width: 576px) {
  .gallery.gallery-columns-4 .gallery-item {
    width: 50%;
  }
}
.gallery.gallery-columns-5 .gallery-item {
  width: 20%;
}
@media screen and (max-width: 575px) {
  .gallery.gallery-columns-5 .gallery-item {
    width: 25%;
  }
}
@media screen and (min-width: 576px) {
  .gallery.gallery-columns-5 .gallery-item {
    width: 50%;
  }
}
.gallery.gallery-columns-6 .gallery-item {
  width: -webkit-calc(100% / 6);
  width: 16.6666666667%;
}
@media screen and (max-width: 575px) {
  .gallery.gallery-columns-6 .gallery-item {
    width: 25%;
  }
}
@media screen and (min-width: 576px) {
  .gallery.gallery-columns-6 .gallery-item {
    width: 50%;
  }
}
.gallery.gallery-columns-7 .gallery-item {
  width: -webkit-calc(100% / 7);
  width: 14.2857142857%;
}
@media screen and (max-width: 575px) {
  .gallery.gallery-columns-7 .gallery-item {
    width: 25%;
  }
}
@media screen and (min-width: 576px) {
  .gallery.gallery-columns-7 .gallery-item {
    width: 50%;
  }
}
.gallery.gallery-columns-8 .gallery-item {
  width: 12.5%;
}
@media screen and (max-width: 575px) {
  .gallery.gallery-columns-8 .gallery-item {
    width: 25%;
  }
}
@media screen and (min-width: 576px) {
  .gallery.gallery-columns-8 .gallery-item {
    width: 50%;
  }
}
.gallery.gallery-columns-9 .gallery-item {
  width: -webkit-calc(100% / 9);
  width: 11.1111111111%;
}
@media screen and (max-width: 575px) {
  .gallery.gallery-columns-9 .gallery-item {
    width: 25%;
  }
}
@media screen and (min-width: 576px) {
  .gallery.gallery-columns-9 .gallery-item {
    width: 50%;
  }
}

.wp-block-table.is-style-stripes.has-subtle-light-gray-background-color tr:nth-child(odd),
.wp-block-table.is-style-stripes tr:nth-child(odd) {
  background-color: var(--c-light-grey);
}

.wp-block-image figcaption {
  color: var(--c-grey);
}

.wp-block-embed figcaption {
  margin-bottom: 0;
}

.wp-block-quote.is-large,
.wp-block-quote.is-style-large {
  margin: 30px auto 30px;
  padding: 2rem 0;
}
.wp-block-quote.is-large *:not(cite),
.wp-block-quote.is-style-large *:not(cite) {
  font-size: 2.5rem !important;
}
.wp-block-quote.is-large cite::before,
.wp-block-quote.is-style-large cite::before {
  display: none;
}

.wp-block-pullquote {
  padding: 2rem 0;
}
.wp-block-pullquote blockquote {
  max-width: 950px;
  padding-right: 15px;
  padding-left: 15px;
}
.wp-block-pullquote.has-background blockquote {
  border: none;
}

.blocks-gallery-caption,
.wp-block-embed figcaption,
.wp-block-image figcaption {
  font-size: 13px;
}

.wp-block-quote.is-large cite,
.wp-block-quote.is-large footer,
.wp-block-quote.is-style-large cite,
.wp-block-quote.is-style-large footer {
  font-size: 13px;
}

.wp-block-cover {
  color: var(--c-light);
}
.wp-block-cover a:hover {
  opacity: 0.7;
}

.wp-block-button.aligncenter {
  clear: both;
}

.wp-block-cover,
.wp-block-cover-image {
  margin-bottom: 15px;
}

.wp-block-archives a,
.wp-block-categories a {
  color: inherit;
}
.wp-block-archives a:hover,
.wp-block-categories a:hover {
  color: var(--c-active);
}

.wp-block-latest-posts li {
  list-style: none;
}
.wp-block-latest-posts li a {
  font-weight: 600;
}
.wp-block-latest-posts li time {
  font-size: 13px;
  color: var(--c-grey);
}

.wp-block-latest-comments.has-excerpts .wp-block-latest-comments__comment-author {
  font-weight: 600;
}
.wp-block-latest-comments__comment-author {
  font-weight: 600;
}
.wp-block-latest-comments__comment-date {
  font-size: 13px;
  margin-top: 6px;
  color: var(--c-grey);
}

.wp-block-search form {
  display: flex;
}
.wp-block-search input[type=search] {
  border: 1px solid rgba(var(--ca-dark), 0.15);
  line-height: 35px;
  padding: 5px 20px;
  width: calc(100% - 57px);
  outline: none;
}
.wp-block-search button {
  border: 1px solid var(--c-active);
  position: relative;
  z-index: 10;
  font-weight: 500;
  padding: 5px 15px;
  background-color: var(--c-active);
  color: var(--c-light);
  cursor: pointer;
  transition: all 0.3s ease;
}
.wp-block-search button:hover {
  border: 1px solid var(--c-dark);
  background: var(--c-dark);
}

.wp-block-cover-image .wp-block-cover-image-text,
.wp-block-cover-image .wp-block-cover-text,
.wp-block-cover-image h2,
.wp-block-cover .wp-block-cover-image-text,
.wp-block-cover .wp-block-cover-text,
.wp-block-cover h2 {
  font-size: 36px;
}
.wp-block-cover-image .wp-block-cover-image-text *,
.wp-block-cover-image .wp-block-cover-text *,
.wp-block-cover-image h2 *,
.wp-block-cover .wp-block-cover-image-text *,
.wp-block-cover .wp-block-cover-text *,
.wp-block-cover h2 * {
  color: inherit;
}

.wp-block-media-text .wp-block-media-text__content {
  padding-top: 15px;
}

.wp-block-separator {
  border: none;
  border-bottom: 1px solid var(--c-dark);
  margin: 1.65em auto;
  clear: both;
}
.wp-block-separator.is-style-wide {
  border-bottom-width: 1px;
}

hr.wp-block-separator:not(.is-style-wide):not(.is-style-dots) {
  max-width: 100px;
  border: none;
  border-bottom: 1px solid var(--c-dark);
  margin: 25px auto;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
  background-color: #F5F5F5;
}

::-webkit-scrollbar {
  width: 15px;
  background-color: #F5F5F5;
}

::-webkit-scrollbar-thumb {
  background-clip: padding-box;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
  background-color: #CBCBCB;
}

.elementor-widget-container > p:not(:last-child) {
  margin-bottom: 10px;
}